@mixin ul-clear {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

@mixin link-clear {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: $black;
}

@mixin inter-regular {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
  color: $black;
  font-size: 16px;
  line-height: 22px;

}

@mixin inter-medium {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  margin: 0;
  color: $black;
  font-size: 16px;
  line-height: 16px;
}

@mixin inter-semibold {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 600;
  margin: 0;
  color: $black;
  font-size: 42px;
  line-height: 42px;
}

@mixin inter-bold {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 700;
  margin: 0;
  color: $black;
  font-size: 24px;
  line-height: 24px;
}


